@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.isCompleted {
  color: grey;
  text-decoration: line-through;
}

.isNotCompleted {
  color: #333;
}
.AddTodo{display:flex;justify-content:space-between;width:100%}.AddTodo input,.AddTodo button{height:3rem;border-radius:.2rem}.AddTodo input{width:80%;margin-right:1rem;padding:1rem;border:1px solid rgba(0,0,0,.3);outline:none;transition:background-color .3s}.AddTodo input:focus{background-color:#eee}.AddTodo button{width:100px;background-color:#6b8e23;border-color:transparent;color:#fff}
.TodoList{width:100%;padding:2rem;background-color:#eee}
.Todo{display:flex;align-items:center;margin-bottom:1rem;padding:.5rem;border-bottom:1px solid rgba(0,0,0,.2)}.Todo input{margin-right:.4rem}.Todo button{margin-left:auto;background-color:crimson;padding:.5rem;color:#fff}
.global-spacing,.App main,.App header{width:100%;padding:1.5rem}@media screen and (min-width: 768px){.global-spacing,.App main,.App header{padding:1.5rem 2rem}}@media screen and (min-width: 992px){.global-spacing,.App main,.App header{width:50%}}*{box-sizing:border-box}body{font-family:"Roboto",sans-serif;font-size:16px}ul{margin:0;padding:0;list-style-type:none}button{border:none;outline:none;text-transform:uppercase}button:hover{cursor:pointer}.App{display:flex;flex-direction:column;align-items:center}.App header{text-align:center}.App header h1{font-size:1.25rem}.App section{margin-bottom:2rem}.App .clear-btn{display:flex;justify-content:center;align-items:center;width:100px;height:2rem;margin:2rem .5rem 0 auto;padding:.5rem;background-color:#6495ed;color:#fff}
